import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { NotFoundComponent } from '../pages/miscellaneous/not-found/not-found.component';
import { EximDashboardComponent } from '../pages/exim-dashboard/exim-dashboard.component';
import { EximDashboardPageComponent } from './exim-dashboard.component';
import { DashboardDemoComponent } from './dashboard-demo/dashboard-demo.component';


const routes: Routes = [{
  path: '',
  component: EximDashboardPageComponent,
  children: [
    {
      path: 'tat',
      component: EximDashboardComponent,
    },
    {
      path: 'tat1',
      component: DashboardDemoComponent,
    },
    {
      path: '',
      redirectTo: 'tat',
      pathMatch: 'full',
    },
    {
      path: '**',
      component: NotFoundComponent,
    },
  ],
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class EximDashboardPageRoutingModule {
}
